@media (min-width:200px){
    .card{
        width: 49%;
        height: 500px;
        border-radius: 4px;
        margin: 6px;
    }
    .card:hover{
        padding: 5px;
    }
    
    .card .h2{
        font-size: 1.1rem;
        padding: 5px;
    }
    .card svg{
        font-size: 1.7rem;
       
    }
    .card .p{
        font-size: .8rem;
    } 
}
@media (min-width:672px){
    .card{
        border-radius: 6px;
        margin: 7px;
        width: 33%;
        height: 430px;
    }
    .card:hover{
        padding: 7px;
    }
    
    .card .h2{
        font-size: 1.4rem;
        padding: 7px;
    }
    .card svg{
        font-size: 2.2rem;
       
    }
    .card .p{
        font-size: 1rem;
    } 
}
@media (min-width:822px){
    .card{
        border-radius: 8px;
        margin: 8px;
        width: 33%;
        height: 380px;
    }
    .card:hover{
        padding: 7px;
    }
    
    .card .h2{
        font-size: 1.5rem;
        padding: 8px;
    }
    .card svg{
        font-size: 3rem;
       
    }
    .card .p{
        font-size: 1.1rem;
    } 
} 
.card{
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 2px solid #119fbfd3;
    border-radius: 10px;
    margin: 10px;
}
.card:hover{
    background-color: #119fbf3f;
    padding: 10px;
}

.card .h2{
    font-size: 1.8rem;
    color: #023059;
    text-align: center;
    padding: 10px;
}
.ico{
    font-size: 4rem;
    color: #F2A413;  
}
.card .p{
    font-size: 1.1rem;
    color: #023059;
    padding: 10px;
    text-align: center;
}