.cart{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #01213d;
}
.cart div h3{
    color: #F2E205; 
    font-size: 2rem; 
    text-align: center;
    padding: 15px;
}
.cart div p{
    color: #bde8f1; 
    font-size: 1.1rem; 
    text-align: center;
    padding: 15px;
}
.fund{
    background-color: #488F31;
    border-radius: 100px;
    margin: 0px 20px;
    width: 90%;
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.329);
}
.img{
    width: 90%;
}
.fond{
   
    border-radius: 100px;
    margin: 0px 20px;
    width: 90%;
    box-shadow: 5px 5px 7px #498f318f;;
}
