@media (min-width:200px){
    .centra h1{
        font-size: 1.1rem;
        margin: 1rem 1rem;
    }
    .centra label{
        font-size: .9rem;
        
    }
    .centra input{
        width: 100%;
        padding: .3rem;
        margin:auto;
    }
    .centra button{
        padding: .2rem;
    }
    .centra button:hover{
        padding: .3rem;
    }
}
@media (min-width:500px){
    .centra h1{
        font-size: 1.4rem;
        margin: 1rem 1rem;
    }
    .centra label{
        font-size: 1rem;
        
    }
    .centra input{
        width: 140%;
        padding: .3rem;
        margin:auto;
    }
    .centra button{
        padding: .3rem;
    }
    .centra button:hover{
        padding: .4rem;
    }
}
@media (min-width:780px){
    .centra h1{
        font-size: 1.7rem;
        margin: 1rem 1rem;
    }
    .centra label{
        font-size: 1.1rem;
        
    }
    .centra input{
        width: 170%;
        padding: .4rem;
        margin:auto;
    }
    .centra button{
        padding: .4rem;
    }
    .centra button:hover{
        padding: .5rem;
    }
}
@media (min-width:1030px){
    .centra h1{
        font-size: 2.2rem;
        margin: 1.3rem 1.2rem;
    }
    .centra label{
        font-size: 1.2rem;
        
    }
    .centra input{
        width: 220%;
        padding: .5rem;
        margin:auto;
    }
    .centra button{
        padding: .5rem;
       
    }
    .centra button:hover{
        padding: .6rem;
    }
}
.centra{
    margin:auto;
    display: flex;
    flex-direction: column;
    flex-direction: column;
    align-items: baseline;
    min-height: 65%;
    flex-wrap: wrap;
    align-content: center;
}
.centra h1{
    color: #023059;
}
.centra label{
    font-weight: 600;
    color: #081f5fd3;
}
.centra button{
    background-color: #023059;
    color: aliceblue;
    margin-bottom: 10px;
}
.centra button:hover{
    background-color:#EEE400 ;
    color: #023059;
}

