@media (min-width:200px){
   .controle{
      padding: .2rem ;
      border-radius: 3px;
      font-size: .9rem;
   } 
   .controle:hover{
      padding:.3rem ;
   }
}
@media (min-width:600px){
   .controle{
      padding: .3rem ;
      border-radius: 4px;
      font-size: 1rem;
   } 
   .controle:hover{
      padding:.4rem ;
   }
}
@media (min-width:900px){
   .controle{
      padding: .4rem ;
      border-radius: 5px;
      font-size: 1.1rem;
   } 
   .controle:hover{
      padding:.5rem ;
   }
}
.controle{
   background-color: #EEE400;
   color: #023059;
   border: none;
   transition: .6s;
   cursor: pointer;
}
.controle:hover{
   background-color:#ffffff  ;
   color: #023059  ;
}
