@media (min-width:200px){
    .footer a{
        font-size: 9rem;
    }
}

.footer{
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #023059;
    color:azure;
   
}
.footer a{
    text-decoration: none;
    font-size: 1.1rem;
    font-weight: bold;
    color:#EEE400;
    cursor: pointer;
}
