@media (min-width:200px){
.nav h1{
    font-size: 1.5rem; 
}
.nav p{
    font-size: .7rem;
}
.nav div img{
    width: 10%;
}
.nav .div{
    display: none;
}
.rep{
    display: none;
}
}
@media (min-width:500px){
    .rep{
        display: block;
    }
}
@media (min-width:680px){
    .nav .div{
        display: flex;
    }
    .nav h1{
        font-size: 2rem; 
    }
    .nav p{
        font-size: .9rem;
    }
    .nav div img{
        width: 12%;
    }
    .nav .div a{
        font-size: 1.1rem;
        font-weight: 500;
        margin-right: 12px;
     }
     .nav .div a:hover{
         padding: .2rem;
         border-radius: 4px;
     }
    
}
@media (min-width:980px){
    .nav h1{
        font-size: 2.7rem; 
    }
    .nav p{
        font-size: 1.1rem;
    }
    .nav div img{
        width: 15%;
    }
    .nav .div a{
        font-size: 1.3rem;
        font-weight: 600;
        margin-right: 20px;
     }
     .nav .div a:hover{
         padding: .2rem;
         border-radius: 5px;
     }
}
.nav{
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background-color: #023059;
    color:azure;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.596);
    position: sticky;
    top: 0;
     
}
.nav h1{
    font-weight: bold;
    font-family: 'Noto Serif', serif;
}
.nav .divlogo{
    display: flex;
}
.nav .div a{
   text-decoration: none;
   color: aliceblue;
   transition: .6s;
   cursor: pointer;
}
.nav .div a:hover{
    background-color: #EEE400;
    color: #035AA6;
}

.nav p{
    text-align: center;
}
.fa{
    display: none;
}
