
@media (min-width:200px){
    img{
       width: 100%;
    }  
    .c1{
        display: none;
    }
    .c2{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}
@media (min-width:292px){
    img{
       width: 100%;
       
    } 
    .centra{
        margin-bottom: -115px;
    }
}
@media (min-width:672px){
    img{
       width: 100%;
    } 
    .c1{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .c2{
        display: none;
    } 
}
@media (min-width:1000px){
    img{
       width: 85%;
    }  
}
@media (min-width:1200px){
    img{
       width: 92%;
       margin-left: 3%;
    }  
   
}
.centra{
    width: 1349px;
    min-height: 75vh;
    overflow: hidden;
    margin: 0px auto;
    transition: 2s;
  
}
.centra .container{
    display: flex;
    transition: 2s; 
    justify-content: space-between; 
}
.img{
    background-size:cover;
    transition: 2s;
}
.container{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transition: 2s;
}
.container2{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    transition: 2s;
}


