@media (min-width:200px){
    .aside{
        display: block;
    }
    .aside header{
        font-size: 1.5rem;
    }
    .aside header:hover{
        font-size: 1.7rem;
    }
    .aside nav{
        padding: 2px; 
        border-radius: 3px;
    }
    .fa{
        font-size: .9rem;
    }  
    .fa:hover{
        font-size: 1rem;
        padding: 2px 3px;
        border-radius: 2px;
}
}
@media (min-width:680px){
    .aside{
        display: none;
    } 
}
.aside{
    cursor: pointer;
}

.aside header:hover{
    color: #bdbcbc;
}
.aside nav{
    display: flex;
    flex-direction: column;
}
.fa{
    font-weight: 300;
    color: #fff;
    text-decoration: none;
    transition: .5s;
}  
.fa:hover{
    color: #fbff01; 
    background-color: rgba(127, 251, 255, 0.301);
    border-bottom: 1px solid #fbff01;
}

